import {lazy, Suspense} from 'react';
import {Routes, Route} from "react-router-dom";

const HomePage = lazy(() => import('./pages/HomePage/HomePage'));
const BookListPage = lazy(() => import("./pages/BookListPage/BookListPage"));
const PhonebookPage = lazy(() => import("./pages/PhonebookPage/PhonebookPage"));
const PostsSearchPage = lazy(() => import('./pages/PostsSearchPage/PostsSearchPage'));
const SinglePostPage = lazy(() => import('./pages/SinglePostPage/SinglePostPage'));
const SinglePostCommentsPage = lazy(() => import("./pages/SinglePostCommentsPage/SinglePostsCommentsPage"));
const NotFoundPage = lazy(() => import("./pages/NotFoundPage/NotFoundPage"));

const UserRoutes = () => {
    return (
        <Suspense fallback={<p>...Load page</p>}>
            <Routes>
                <Route path="/services/" element={<HomePage />} />
                <Route path="/services/booklist" element={<BookListPage />} />
                <Route path="/services/phonebook" element={<PhonebookPage />} />
                <Route path="/services/posts" element={<PostsSearchPage />} />
                <Route path="/services/posts/:id" element={<SinglePostPage />}>
                    <Route path="comments" element={<SinglePostCommentsPage />} />
                </Route>
                <Route path="/services*" element={<NotFoundPage />} />
            </Routes>
        </Suspense>

    )
}

export default UserRoutes;